import { FETCH_ANNOUNCMENTS, FETCH_ANNOUNCMENTS_WITH_FILTER, POST_FAVORITE } from "./actions";
import { SET_ANNOUNCMENTS, SET_FILTER, FILTER_TYPE, SET_FAVORITE, SET_CURRENT_ANNOUNCMENT_ID } from "./mutations";
import { fetchAnnouncmentsRequest, postFavoriteRequest } from "../services/api";

import {
    START_ASYNC_TASK,
    FINISH_ASYNC_TASK
} from "@/vuex/mutations";

const announcmentsModule = {
    state: {
        announcments: [],
        priceRange: [600, 3200],
        selectedPriceRange: [600, 3200],
        areaRange: [20, 80],
        selectedAreaRange: [20, 80],
        roomsCounts: ["1", "2", "3", "4+"],
        selectedRoomsCount: 2,
        cities: ['Warszawa', 'Kraków', 'Białystok', 'Lublin'],
        selectedCity: 'Warszawa',
        currentAnnouncmentId: '',
    },
    actions: {
        [FETCH_ANNOUNCMENTS]: async ({ commit, state }) => {
            commit(START_ASYNC_TASK);
            try {
                const { data } = await fetchAnnouncmentsRequest();
                if (data.success) {
                    const announcments = data.announcments;
                    commit(SET_ANNOUNCMENTS, { announcments });
                }
            } catch (e) { }
            commit(FINISH_ASYNC_TASK);
        },
        [FETCH_ANNOUNCMENTS_WITH_FILTER]: async ({ dispatch, commit }, payload) => {
            commit(SET_FILTER, { filterType: payload.filterType, filterValue: payload.filterValue })
            dispatch(FETCH_ANNOUNCMENTS)
        },
        [POST_FAVORITE]: async ({ commit }, payload) => {
            commit(SET_FAVORITE, payload);
            try {
                const { data } = await postFavoriteRequest();
                if (data.success) {
                }
            } catch (e) { }
        }
    },
    mutations: {
        [SET_ANNOUNCMENTS]: (state, { announcments }) => {
            state.announcments = announcments;
        },
        [SET_FILTER]: (state, { filterType, filterValue }) => {
            switch (filterType) {
                case FILTER_TYPE.CITY:
                    state.city = filterValue;
                    break;
                case FILTER_TYPE.PRICE:
                    state.price = filterValue;
                    break;
                case FILTER_TYPE.AREA:
                    state.area = filterValue;
                    break;
                case FILTER_TYPE.ROOMS:
                    state.roomsCount = filterValue;
                    break;
            }
        },
        [SET_FAVORITE]: (state, { id }) => {
            let ann = state.announcments.find(ann => ann.id === id)
            if (ann) {
                ann.isFavorite = !ann.isFavorite
            }
        },
        [SET_CURRENT_ANNOUNCMENT_ID]: (state, { id }) => {
            state.currentAnnouncmentId = id
        }
    }
};

export default announcmentsModule;
