<template>
<div class="main-content">
  <filter-toolbar v-show="isListView" :class="{'hidden-xs-only': !layoutFilterToolbarDisplayed}"/>
  <v-layout :class="{'list-height': isListView, 'details-height': !isListView}" row wrap>
    <v-flex md7 sm12 :class="{'hidden-sm-and-down': !isListDisplayed, 'hidden-drawer': !isListDisplayed}" style="position: relative">
      <router-view/>
      <list-switch v-show="isListView" :isListDisplayed="isListDisplayed" :switchView="switchView"/>
    </v-flex>
    <v-flex md5 sm12 :class="{'hidden-sm-and-down': isListDisplayed, 'map-expanded': !isListDisplayed}">
      <flats-map/>
    </v-flex>
  </v-layout>
    <bottom-toolbox class="hidden-md-and-up" :isListDisplayed="isListDisplayed" :switchView="switchView" :switchFilters="switchFilters" />
</div>
</template>

<script>
  import { mapState } from "vuex";

  import FlatsMap from "./scenes/Map/Map.vue";
  import FilterToolbar from "./scenes/FilterToolbar/FilterToolbar.vue";
  import ListSwitch from "./components/ListSwitch.desktop.vue";
  import BottomToolbox from "./components/BottomToolbox.mobile.vue";

  export default {
    name: "landing",
    data: function() {
      return {
        isListDisplayed: true,
        layoutFilterToolbarDisplayed: false
      };
    },
    computed: {
      isListView() {
        return this.$route.name === "flats" || this.$route.name === "rooms";
      }
    },
    methods: {
      switchView: function() {
        this.isListDisplayed = !this.isListDisplayed;
        this.layoutFilterToolbarDisplayed = false;
      },
      switchFilters: function() {
        this.layoutFilterToolbarDisplayed = !this.layoutFilterToolbarDisplayed;
      }
    },
    components: {
      FlatsMap,
      FilterToolbar,
      ListSwitch,
      BottomToolbox
    }
  };
</script>

<style lang="sass" scoped>
  .main-content
    height: 94%
    @media screen and (max-width: 1000px)
      height: 84%
    @media screen and (max-width: 600px)
      height: 90%
  .list-height
    height: 88%
    @media screen and (max-width: 1000px)
      height: 80%
    @media screen and (max-width: 600px)
      height: 88%
  .details-height
    height: 100%
    @media screen and (max-width: 1000px)
      height: 100%
    @media screen and (max-width: 600px)
      height: 88%
  .hidden-drawer
    max-width: 0%
  .map-expanded
    min-width: 100%
</style>
