<template>
    <v-card dark color="secondary">
        <gmap-map
            :center="{lat: 52.237049, lng: 21.017532}"
            :zoom="12"
            :options="mapOptions"
            @bounds_changed="updateRectangle($event)"
            style="width: 100%; height: 100%"
            >
            <gmap-cluster
            :styles="clusterStyles"
            >
              <gmap-marker
                v-for="announcment in announcments"
                :key="announcment.id"
                :position="announcment.location"
                :clickable="true"
                :icon="announcment.isFavorite ? favoriteIcon : customIcon"
                @click="center=m.position"
              />
            </gmap-cluster>
        </gmap-map>
    </v-card>
</template>

<script>
  import ExactMapMarker from "./components/ExactMapMarker.vue";
  import mapStyles from "./mapStyles.js";
  import clusterStyles from "./clusterStyles.js";
  import customMarker from "@/assets/customMarker.png";
  import favoriteMarker from "@/assets/favoriteMarker.png";
  import { mapState } from "vuex";
  import _ from "lodash";
  import {} from "marker-clusterer-plus";
  const logBoundingBox = _.debounce(event => console.log(event.j, event.l), 500);

  export default {
    data: function() {
      return {
        mapOptions: {
          mapTypeControl: false,
          styles: mapStyles
        },
        clusterStyles: clusterStyles,
        customIcon: customMarker,
        favoriteIcon: favoriteMarker
      };
    },
    computed: {
      ...mapState({
        announcments: state => state.announcments.announcments
      })
    },
    methods: {
      updateRectangle(event) {
        if (event) {
          logBoundingBox(event);
        }
      }
    },
    components: {
      ExactMapMarker
    }
  };
</script>

<style lang="sass" scoped>
    .v-card
      height: 100%
    .vue-map-container
      position: static !important
</style>
