 <template>
 <v-navigation-drawer
      v-model="isDrawerShown"
      absolute
      temporary
      class="dr-drawer"
    >
      <v-list class="pa-1" v-if="isSignedIn">
        <v-list-tile avatar to="/profile" active-class="dr-action-active">
          <v-list-tile-avatar>
            <img src="https://blog.archonia.com/content/images/2016/05/batmanIcon.jpg">
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title class="dr-username">Bruce Wayne</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
      <v-list class="pa-1" v-else>
        <v-list-tile>
          <v-list-tile-content>
            <v-list-tile-title class="dr-brand">Flat Rent</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
       
        <v-divider class="ma-2" style="background: #fb7600"/>


      <v-list-tile to="/" active-class="dr-action-active">
          <v-list-tile-action>
            <v-icon large color="#fb7600">business</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="dr-action">Find Flat</v-list-tile-title>
          </v-list-tile-content>
       </v-list-tile>

        <v-list-tile to="/room" active-class="dr-action-active">
          <v-list-tile-action>
            <v-icon large color="#fb7600">weekend</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="dr-action">Find Room</v-list-tile-title>
          </v-list-tile-content>
       </v-list-tile>

        <v-list-tile to="/roommates" active-class="dr-action-active">
          <v-list-tile-action>
            <v-icon large color="#fb7600">person_pin</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="dr-action">Find Roommate</v-list-tile-title>
          </v-list-tile-content>
       </v-list-tile>

        <v-divider class="ma-2" style="background: #fb7600"/>

        <v-list-tile to="/addnew" active-class="dr-action-active">
          <v-list-tile-action>
            <v-icon large color="#fb7600">add_to_photos</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="dr-action">Add New</v-list-tile-title>
          </v-list-tile-content>
       </v-list-tile>

        <v-list-tile class="dr-btn-container" v-if="!isSignedIn" >
                  <v-btn outline color="#fb7600" to="/signin">Sign In</v-btn>
                  <v-btn outline color="#fb7600" to="/signup">Sign Up</v-btn>
              </v-list-tile>
         <v-list-tile class="dr-signout-container" @click="signOut" v-else>
            <v-btn outline color="#fb7600">Sign Out</v-btn>
        </v-list-tile>
 
      </v-list>
    </v-navigation-drawer>
 </template>

<script>
import store from "@/store";
import { mapState } from "vuex";
import { HIDE_DRAWER } from "@/vuex/mutations";
import { SIGN_OUT } from "@/vuex/actions";

export default {
  computed: {
    ...mapState(["isSignedIn"]),
    isDrawerShown: {
      get() {
        return store.state.isDrawerShown;
      },
      set(val) {
        if (val === false) {
          store.commit(HIDE_DRAWER);
        }
      }
    }
  },
  methods: {
    signOut() {
      store.dispatch(SIGN_OUT);
    }
  }
};
</script>
 
 <style lang="sass">
      .dr-action-active
        color: #fb7600
      .dr-drawer
        background: #3c3c3b !important
      .dr-username
        color: #fff
        font-weight: bold
        font-size: 18px
      .dr-brand
        color: #fff
        font-weight: bold
        font-size: 32px
      .dr-action
        color: #fff
        font-weight: bold
        font-size: 16px
      .dr-btn-container
        margin: 50px 0
        display: flex
        flex-direction: row
        justify-content: center
      .dr-signout-container
        display: flex
        flex-direction: row
        justify-content: center
        position: absolute
        bottom: 60px
        width: 100%
 </style>
 