<template>
  <div class="ft-toolbar">
      <div class="ft-container">
        <v-select
          :items="cities"
          v-model="selectedCity"
          label="City"
          color="#fb7600"
          solo
          />
      </div>
      <div  class="ft-container">
        <v-range-slider
          label="Price"
          v-model="selectedPriceRange"
          color="#fb7600"
          :min="priceRange[0]"
          :max="priceRange[1]"
          :step="50"
          always-dirty
          thumb-label="always"
          />
      </div>
      <div class="ft-container">
        <v-range-slider
          label="Area"
          v-model="selectedAreaRange"
          color="#fb7600"
          :min="areaRange[0]"
          :max="areaRange[1]"
          :step="1"
          always-dirty
          thumb-label="always"
          />
      </div>
      <div class="ft-container rooms">
       <v-slider
            label="Rooms"
            v-model="selectedRoomsCount"
            color="#fb7600"
            :tick-labels="roomsCounts"
            :max="roomsCounts.length-1"
            step="1"
            ticks="always"
            tick-size="2"
            />
      </div>
      <v-btn color="#fb7600" outline>
        MORE
      </v-btn>
  </div>
</template>

<script>
import { mapState } from "vuex";
import store from "@/store";
import { SET_FILTER, FILTER_TYPE } from "../../vuex/mutations";
import _ from "lodash";

const setFilter = (filterType, val) =>
  store.commit(SET_FILTER, {
    filterType: filterType,
    filterValue: val
  });
const debouncedSetFilterFactory = filterType =>
  _.debounce(val => setFilter(filterType, val), 300);
const setPrice = debouncedSetFilterFactory(FILTER_TYPE.PRICE);
const setArea = debouncedSetFilterFactory(FILTER_TYPE.AREA);

export default {
  computed: {
    ...mapState({
      cities: state => state.announcments.cities,
      priceRange: state => state.announcments.priceRange,
      areaRange: state => state.announcments.areaRange,
      roomsCounts: state => state.announcments.roomsCounts
    }),
    selectedCity: {
      get() {
        return store.state.announcments.selectedCity;
      },
      set(val) {
        setFilter(FILTER_TYPE.CITY, val);
      }
    },
    selectedPriceRange: {
      get() {
        return store.state.announcments.selectedPriceRange;
      },
      set(val) {
        setPrice(val);
      }
    },
    selectedAreaRange: {
      get() {
        return store.state.announcments.selectedAreaRange;
      },
      set(val) {
        setArea(val);
      }
    },
    selectedRoomsCount: {
      get() {
        return store.state.announcments.selectedRoomsCount;
      },
      set(val) {
        store.commit(SET_FILTER, {
          filterType: FILTER_TYPE.ROOMS,
          filterValue: val
        });
      }
    }
  },
};
</script>

<style lang="sass">
      .ft-toolbar
        height: 12%
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: space-around
        background: #fff
      .ft-container
        display: flex
        align-items: flex-end
        height: 100%
        max-height: 100px
        min-width: 200px
        max-width: 12%
      .rooms
        max-width: 20%
      .rooms /deep/ .v-slider__thumb
        background: #fb7600 !important
        border-color: #fb7600 !important
      @media screen and (max-width: 1000px)
        .ft-toolbar
            height: 20%
      @media screen and (max-width: 600px)
        .ft-toolbar
            position: fixed
            z-index: 5
            height: 80%
            flex-direction: column
</style>
