import Vue from "vue";
import Vuex from "vuex";

import authenticationModule from "@/scenes/Authentication/vuex/module";
import announcmentsModule from "@/scenes/Main/vuex/module";

import {
  SET_SIGNED_IN,
  SET_SIGNED_OUT,
  START_ASYNC_TASK,
  FINISH_ASYNC_TASK,
  SHOW_DRAWER,
  HIDE_DRAWER
} from "./vuex/mutations";
import { SIGN_OUT } from "./vuex/actions";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isSignedIn: !!localStorage.getItem("user-token"),
    isLoading: false,
    isDrawerShown: false
  },
  mutations: {
    [SET_SIGNED_IN]: state => {
      state.isSignedIn = true;
    },
    [SET_SIGNED_OUT]: state => {
      state.isSignedIn = false;
    },
    [START_ASYNC_TASK]: state => {
      state.isLoading = true;
    },
    [FINISH_ASYNC_TASK]: state => {
      state.isLoading = false;
    },
    [SHOW_DRAWER]: state => {
      state.isDrawerShown = true;
    },
    [HIDE_DRAWER]: state => {
      state.isDrawerShown = false;
    }
  },
  actions: {
    [SIGN_OUT]: ({ commit }) => {
      commit(SET_SIGNED_OUT);
      localStorage.removeItem("user-token");
    }
  },
  modules: {
    authentication: authenticationModule,
    announcments: announcmentsModule,
  }
});
