<template>
   <v-bottom-nav
      :active.sync="bottomNav"
      :value="true"
      color="#1e1e1e"
    >
      <v-btn
        color="#fff"
        value="filter"
        flat
        @click="switchFilters"
      >
        <span>{{ isListView ? "Filter" : "Contact"}}</span>
        <v-icon >{{ isListView ? "filter_list" : "message"}}</v-icon>
      </v-btn>

      <v-btn
        color="#fb7600"
        flat
        value="switch"
        @click="switchView"
      >
        <span>{{isListDisplayed ? "Go To Map" : isListView ? "Back To List" : "Back To Details"}}</span>
        <v-icon >{{isListDisplayed ? "map" : "arrow_back"}}</v-icon>
      </v-btn>

      <v-btn
        color="#fff"
        value="sort"
        flat
      >
        <span>{{ isListView ? "Sort" : "Share" }}</span>
        <v-icon >{{ isListView ? "sort" : "share" }}</v-icon>
      </v-btn>
    </v-bottom-nav>
</template>

<script>
export default {
  data() {
    return {
      bottomNav: "switch"
    };
  },
  computed: {
      isListView() {
        return this.$route.name === "flats" || this.$route.name === "rooms";
      }
  },
  props: {
    isListDisplayed: Boolean,
    switchView: Function,
    switchFilters: Function
  }
};
</script>

<style lang="sass" scoped>
    .v-bottom-nav
      height: 12% !important
</style>
