/// payload = { email, password }
export const signInRequest = async payload =>
  sleep(() => {
    return {
      data: {
        accessToken: "xddd",
        success: true
      }
    };
  }, payload);

/// payload = { email, password }
export const signUpRequest = async payload =>
  sleep(() => {
    return {
      data: {
        accessToken: "xddd",
        success: true
      }
    };
  }, payload);

function sleep(fn, par) {
  return new Promise(resolve => {
    setTimeout(() => resolve(fn(par)), 2000);
  });
}