import { SIGN_IN, SIGN_UP } from "./actions";
import { signInRequest, signUpRequest } from "../services/api";
import router from "@/router";

import {
  SET_SIGNED_IN,
  START_ASYNC_TASK,
  FINISH_ASYNC_TASK
} from "@/vuex/mutations";

const authenticationModule = {
  actions: {
    [SIGN_IN]: async ({ commit }, { payload }) => {
      commit(START_ASYNC_TASK);
      try {
        const { data } = await signInRequest(payload);
        if (data.success) {
          const token = data.accessToken;
          localStorage.setItem("user-token", token);
          commit(SET_SIGNED_IN);
          router.push("/");
        }
      } catch (e) { }
      commit(FINISH_ASYNC_TASK);
    },
    [SIGN_UP]: async ({ commit }, payload) => {
      commit(START_ASYNC_TASK);
      try {
        const { data } = await signUpRequest(payload);
        if (data.success) {
          router.push("/signin");
        }
      } catch (e) { }
      commit(FINISH_ASYNC_TASK);
    }
  }
};

export default authenticationModule;
